.modal__overlay {
  position: fixed;
  top: 0; left: 0;
  width: 100vw; height: 100vh;
  background: rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}

.modal {
  width: 576px;
  min-height: 316px;
  background: white;
  border-radius: 20px;
  padding: 46px 92px 47px 92px;
  text-align: center;
  font-family: var(--font-secondary);
  position: relative; 
}

.modal__text {
  font-family: var(--font-secondary);
  font-size: 14px;
  color: var(--colorTextPrimary);
  text-align: center;
  margin-bottom: 42px;
  // padding: 0 10px;
}

.modal__label {
  font-family: var(--font-secondary);
  font-size: 14px;
}

.modal__input {
  width: 260px;
  height: 40px;
  font-size: 14px;
  padding: 10px;
  margin-top: 18px;
  margin-bottom: 18px;
  border-radius: 20px;
  border: 1px solid #ccc;
  background-color: $colorBgForm;
  color: $colorTextForm;
  font-family: var(--font-primary);
} 

.modal__button {
  width: 120px;
  height: 40px;
  border: none;
  border-radius: 25px;
  background: black;
  color: white;
  cursor: pointer;
  font-size: 14px;
  box-shadow: $shadowForm;
}

.modal .close-btn {
  position: absolute;
  top: 15px;
  right: 20px;
  font-size: 24px;
  cursor: pointer;
  border: none;
  background: transparent;
}


