.modal__icons {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

.modal__icons--left {
  left: -40px; /* або більше — регулюй */
  top: 0;
}

.modal__icons--right {
  right: -40px;
  top: 0;
}

.modal__icon__container--lefttop {
  position: relative;
  top: 185px;
  left: 90px;
}

.modal__icon__container--leftbotton {
  position: relative;
  bottom: 46px;
  left: 120px;
}

.modal__icon {
  width: 32px;
  height: 32px;
}
.modal__icon__container--righttop {
  position: relative;
  top: 185px;
  left: -90px;
}

.modal__icon__container--rightbotton {
  position: relative;
  bottom: 46px;
  left: -120px;
}